// HOME PAGE BANNER SLIDER

const bannerSlider = new Swiper(".bannerSlider", {
  slidesPerView: 1,
  grabCursor: false,
  spaceBetween: 0,
  keyboard: {
    enabled: true,
  },
  // hashNavigation: {
  //   watchState: true,
  // },
  pagination: {
    el: ".swiper-pagination",
    clickable: true,
  },
  breakpoints: {
    320: {
      slidesPerView: 1.1,
      spaceBetween: 10,
    },

    768: {
      slidesPerView: 1,
    },
  },
  navigation: {
    nextEl: ".swiper-button-next",
    prevEl: ".swiper-button-prev",
  },
});

$(document).ready(function () {
  $(".zw-play-btn").on("click", function () {
    if ($(this).attr("data-click") == 0) {
      $(this).attr("data-click", 1);
      $("#video")[0].pause();
      $(".zw-play-btn").addClass("pause");
    } else {
      $(this).attr("data-click", 0);
      $("#video")[0].play();
      $(".zw-play-btn").removeClass("pause");
    }
  });
});

$(document).ready(function () {
  $(".zw-video-wraper").hover(
    function () {
      $(this).addClass("hover");
    },
    function () {
      $(this).removeClass("hover");
    }
  );
});

// Onclick mouse cursor scroll down

document
  .getElementById("banner-section")
  .scrollTo({ behavior: "smooth", top: 0 });




// Video Sound making mute and unmute
$(".zw-videomute-wrap").on("click", function (e) {
  var video = document.getElementById("video");
  if (!video.muted) {
    document.getElementById("muteicon").src = "./img/home/sound-mute.svg";
    // video.muted <- not assigned to true
    video.muted = true;
  } else {
    document.getElementById("muteicon").src = "./img/home/unmute.svg";
    // video.muted == false <- wrong use of equality operator
    video.muted = false;
  }
});